<template>
  <div
    class="hidden w-full md:w-1/3 z-0 px-8 md:px-0 md:py-6 invoice-container sticky md:block"
  >
    <div
      class="text-grey-darker bg-gray-100 rounded-b md:rounded-b-none md:rounded-l shadow-lg overflow-hidden"
    >
      <div
        class="bg-main text-lg text-kp-500 font-medium p-4 border-b border-kp-500 tracking-wide flex justify-between items-center"
      >
        Total Pinjaman
        <div class="relative">
          Tenor:
          <div
            class="inline-flex items-center shadow border rounded cursor-pointer bg-white"
          >
            <select
              class="py-0 pl-2 pr-5 flex-1 focus:outline-none bg-transparent z-10"
              v-model="tenor"
              @input="selectTenor"
            >
              <option
                v-for="(list, i) in listTenor"
                :key="i"
                class="text-gray-700"
                :value="list.tenor"
              >
                {{ list.tenor }}
              </option>
            </select>
            <svg
              class="absolute right-0 w-2 h-2 mr-2"
              ref="iconSelect"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 207 121.34"
              style="transform: rotate(180deg)"
            >
              <title>Icon</title>
              <path
                d="M629.23,600.67a14.23,14.23,0,0,1-10.29-4.38L540,514.2l-78.93,82.09a14.28,14.28,0,1,1-20.58-19.79l89.22-92.79a14.28,14.28,0,0,1,20.58,0l89.22,92.79a14.28,14.28,0,0,1-10.29,24.17Z"
                transform="translate(-436.5 -479.33)"
              />
            </svg>
          </div>
        </div>
      </div>
      <div class="bg-grey-lightest flex flex-wrap text-gray-700">
        <div
          v-for="n in invoices"
          :key="n.id"
          class="pt-2 px-6 w-full flex flex-wrap"
        >
          <header class="w-2/3">
            <div class="truncate">
              {{ n.category_id }} - {{ n.item_description }}
            </div>
          </header>
          <article class="w-1/3 text-right font-medium">
            Rp {{ $root.convertRibu(n.otr * n.qty) }}
          </article>
          <footer class="w-full text-xs text-gray-600">
            <div>Harga: Rp {{ $root.convertRibu(n.otr) }}</div>
            <div>Jumlah: {{ $root.convertRibu(n.qty) }}</div>
          </footer>
        </div>
      </div>
      <div class="px-6 py-2 border-t">
        Subtotal
        <span class="font-medium float-right">
          Rp
          {{
            invoices.reduce((tot, item) => {
              return tot + item.qty * item.otr;
            }, 0)
          }}
        </span>
      </div>
      <div class="border-t-2 border-dashed px-6 py-2 flex flex-wrap">
        <header class="w-2/3">
          <div class="truncate font-medium">Cicilan</div>
        </header>
        <article class="w-1/3 text-right font-medium text-kp-500">
          Rp 4.607.500
        </article>
        <footer class="w-full text-xs text-gray-600">
          12 bulan x Rp 492.450
        </footer>
      </div>
    </div>
  </div>
</template>

<script>
import { store, mutations } from "../store";

export default {
  data() {
    return {
      invoices: [],
      listTenor: [],
      tenor: 12
    };
  },
  computed: {
    cat_id: {
      get() {
        return store.cat_id;
      },
      set(val) {
        mutations.setCatId(val);
      }
    },
    supplier_key: {
      get() {
        return store.supplier_key;
      },
      set(val) {
        mutations.setSupplierKey(val);
      }
    }
  },
  methods: {
    selectTenor() {}
  },
  async mounted() {
    await this.$http
      .get("api/v1/master/asset", {
        withCredentials: true,
        headers: {
          Authorization: `${this.$root.getParameterByName("token")}`
        }
      })
      .then(res => res.json())
      .then(resAsset => {
        this.invoices = resAsset.data.items;
        this.supplier_key = resAsset.data.item.supplier_key;
        this.cat_id = resAsset.data.items[0].category_id;
      })
      .catch(err => err);
    await this.$http
      .get(`api/v1/master/tenor/${this.supplier_key}/${this.cat_id}`, {
        withCredentials: true,
        headers: {
          Authorization: `${this.$root.getParameterByName("token")}`
        }
      })
      .then(res => res.json())
      .then(resTenor => {
        this.listTenor = resTenor.data.items;
      })
      .catch(err => err);
  }
};
</script>
