<template>
  <div class="mb-4">
    <label class="block text-grey-darker text-sm font-bold mb-2" :for="idna">
      {{ label }}
    </label>
    <datepicker
      input-class="shadow appearance-none border border rounded h-12 w-full py-2 px-3 text-grey-darker leading-tight focus:outline-none"
      :id="idna"
      :placeholder="label"
      :minimumView="FullDate ? 'date' : 'month'"
      :maximumView="'year'"
      :initialView="'year'"
      :format="FullDate ? 'dd-MM-yyyy' : 'MM-yyyy'"
      :value="tempVal"
      @selected="pushVal"
      :name="idna"
    />
  </div>
</template>

<script>
import Datepicker from "vuejs-datepicker";

export default {
  props: {
    value: {
      type: String,
      default: null
    },
    label: {
      type: String,
      default: ""
    },
    id: {
      type: String,
      default: null
    },
    FullDate: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    value(val) {
      this.tempVal = val ? new Date(this.value) : val;
    }
  },
  data() {
    return {
      tempVal: this.value ? new Date(this.value) : this.value
    };
  },
  components: {
    Datepicker
  },
  computed: {
    idna() {
      return this.id || this.label.replace(/ /, "");
    }
  },
  methods: {
    pushVal(val) {
      this.$emit("input", this.formatDate(val));
    },
    formatDate(date) {
      let month = "" + (date.getMonth() + 1);
      let day = "" + date.getDate();
      const year = date.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;
      const fixedDate = [year, month];
      if (this.FullDate) {
        fixedDate.push(day);
      }
      return fixedDate.join("-");
    }
  }
};
</script>
