import Vue from "vue";

export const store = Vue.observable({
  province: [],
  realStep: [
    ["personal-page", "address-page", "contact-page"],
    "emergency-contact-page",
    ["job-page", "address-job-page"]
  ],
  checked: [],
  userProvinsi: null,
  userCity: null,
  userKecamatan: null,
  userKelurahan: null,
  userZip: null,
  hasBranch: true,
  cat_id: null,
  supplier_key: null
});

export const mutations = {
  setProvince(provinces) {
    store.province = provinces;
  },
  setUserProvinsi(province) {
    store.userProvinsi = province;
  },
  setUserCity(city) {
    store.userCity = city;
  },
  setUserKecamatan(kecamatan) {
    store.userKecamatan = kecamatan;
  },
  setUserKelurahan(kelurahan) {
    store.userKelurahan = kelurahan;
  },
  setHasBranch(val) {
    store.hasBranch = val;
  },
  setUserZip(val) {
    store.userZip = val;
  },
  setCatId(val) {
    store.cat_id = val;
  },
  setSupplierKey(val) {
    store.supplier_key = val;
  }
};
