<template>
  <div
    id="app"
    class="antialiased text-gray-700"
    :class="{ 'overflow-hidden h-screen': !hasBranch }"
  >
    <daerah-popup v-if="!hasBranch" />
    <div v-else>
      <component :is="$root.isVideo" />
      <kp-header />
      <kp-footer />
      <main
        class="justify-center items-start block md:flex mx-auto px-0 md:px-6 md:py-4"
      >
        <kp-invoice />
        <div
          class="w-full md:w-2/3 relative z-1 bg-gray-100 shadow-lg overflow-visible relative"
        >
          <div
            v-if="$root.stepLoading"
            class="absolute inset-0 z-20 rounded pt-32"
            style="background: rgba(0,0,0,0.25);"
          >
            <div id="loading-wrapper" class="h-auto sticky">
              <div id="loading-text">
                LOADING
              </div>
              <div id="loading-content"></div>
            </div>
          </div>
          <keep-alive>
            <component
              ref="activeForm"
              :is="$root.tabs[$root.cur].componentId"
            />
          </keep-alive>
          <div
            class="flex"
            :class="{ 'justify-between': hasPrev, 'justify-end': !hasPrev }"
          >
            <button
              v-if="hasPrev"
              @click="prevKeun"
              class="focus:outline-none flex items-center justify-center bg-gray-200 w-40 h-16 text-md font-semibold text-gray-800 uppercase bg-main-hover rounded-br"
            >
              <span
                class="font-medium text-gray-700 mr-2"
                style="transform: rotate(180deg)"
              >
                
              </span>
              <span>Prev</span>
            </button>
            <button
              @click="nextKeun"
              class="focus:outline-none flex items-center justify-center bg-gray-200 w-40 h-16 text-md font-semibold text-gray-800 uppercase bg-main-hover rounded-br"
            >
              <span>Next</span>
              <span class="font-medium text-gray-700 ml-2"></span>
            </button>
          </div>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
import KpInvoice from "./components/Invoice.vue";
import KpHeader from "./components/Header.vue";
import KpFooter from "./components/Footer.vue";
import DaerahPopup from "./components/DaerahPopup.vue";
import { store } from "./store";

export default {
  name: "app",
  components: {
    KpInvoice,
    KpHeader,
    KpFooter,
    DaerahPopup
  },
  data() {
    return {
      hasPrev: false
    };
  },
  computed: {
    hasBranch() {
      return store.hasBranch;
    }
  },
  methods: {
    validate(step, data) {
      this.$root.stepLoading = true;
      return this.$http
        .post("api/v1/validate/" + step, {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          },
          json: data
        })
        .then(res => res.json())
        .catch(err => err.response.json());
    },
    nextKeun() {
      let activeComp = null;
      let currentData = null;
      let endPoint = null;
      let cb = null;
      if (
        (this.$root.cur === 0 && this.$refs.activeForm.cur < 2) ||
        (this.$root.cur === 2 && this.$refs.activeForm.cur < 1)
      ) {
        activeComp = this.$refs.activeForm.$children[this.$refs.activeForm.cur];
        currentData = this.$refs.activeForm.$children[this.$refs.activeForm.cur]
          ._self.fixedData;
        endPoint = store.realStep[this.$root.cur][this.$refs.activeForm.cur];
        cb = finalData => {
          if (finalData.data.message === "Success") {
            this.$refs.activeForm.cur += 1;
            window.scrollTo({ top: 100, left: 0, behavior: "smooth" });
            if (this.$refs.activeForm.cur > 0) {
              this.hasPrev = true;
            } else {
              this.hasPrev = false;
            }
          } else {
            throw finalData.data.items;
          }
        };
      } else {
        if (this.$root.cur === 0 || this.$root.cur === 2) {
          activeComp = this.$refs.activeForm.$children[
            this.$refs.activeForm.cur
          ];
          currentData = this.$refs.activeForm.$children[
            this.$refs.activeForm.cur
          ]._self.fixedData;
          endPoint = store.realStep[this.$root.cur][this.$refs.activeForm.cur];
        } else {
          activeComp = this.$refs.activeForm;
          currentData = this.$refs.activeForm._self.fixedData;
          endPoint = store.realStep[this.$root.cur];
        }
        cb = finalData => {
          if (finalData.data.message === "Success") {
            this.$root.cur += 1;
            window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
          } else {
            throw finalData.data.items;
          }
        };
      }
      this.validate(endPoint, currentData)
        .then(cb)
        .catch(err => {
          console.log(activeComp);
          activeComp.errorFields = Object.keys(err);
          // console.log(activeComp.errorFields);
          document
            .querySelector("#" + Object.keys(err)[0])
            .scrollIntoView({ behavior: "smooth", block: "center" });
        })
        .then(() => {
          this.$root.stepLoading = false;
        });
    },
    prevKeun() {
      if (
        this.$root.cur === 0 ||
        (this.$root.cur === 2 && this.$refs.activeForm.cur > 0)
      ) {
        window.scrollTo({ top: 100, left: 0, behavior: "smooth" });
        this.$refs.activeForm.cur -= 1;
        if (this.$refs.activeForm.cur > 0 || this.$root.cur === 2) {
          this.hasPrev = true;
        } else {
          this.hasPrev = false;
        }
      } else {
        this.$root.cur -= 1;
        window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
      }
    }
  }
};
</script>

